.appWrapper {
    display: grid;
    width: 1200px;
    margin: 0 auto;
    grid-template-areas:
    " h h"
    " n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-auto-rows: 1em;
    grid-gap: .2em;
}
